.tab-header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    gap: 5px;
}

.tab-item {
    display: flex;
    padding: 5px;
    cursor: pointer;
    background: #2e6da4;
    border: 3px solid #2e6da4;
    color: #ffffff;
    align-items: center;
}

.tab-item.active {
    border-bottom: 3px solid #1c2157;
}

.tab-item:not(.active):hover {
    border-bottom: 3px solid #ccc;
}

.tab-body {
    border: 1px solid #232b38;
}

.tab-close {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    height: 25px;
    width: 25px;
}

